/* Buttons  */

// Button Extend  
.buttons {
	color: white;
	display: inline-block; 
	padding: 10px 25px 8px 25px;
	text-align: center;
	font-size: 1rem;
	line-height: 1.6;
	text-decoration: none;
	white-space: nowrap;
	background: $three;
	border-radius: 30px;
	border: 1px solid $three;
	cursor: pointer;
	font-weight: 600;
	margin: 5px 0;
  box-shadow: 0px 1px 1px rgba(0,0,0,.1);
	text-transform: uppercase;
	font-family: 'Poppins', Helvetica Neue, Helvetica, Arial, sans-serif;
}
.buttons-hover {
	border-bottom: 0;
	color: $three;
	background: transparent;
	border: 1px solid $three;
  text-decoration: none;
}
// Button Output
a.button,
.button,
button,
.posts-links a {
	@extend .buttons;
	&:hover,
	&:focus {
		@extend .buttons-hover;
	}
}
input {
	&[type="submit"],
	&[type="reset"],
	&[type="button"] {
		@extend .buttons;
		&:hover,
		&:focus {
			@extend .buttons-hover;
		}
	}
}
